body {
  background-color: #a9a9a9;
}

a {
  color: #eee;
  text-decoration: none;
}

.search-form {
  background-color: #4056b4;
  justify-content: center;
  padding: 15px;
  display: flex;
}

[type="text"] {
  height: 30px;
  width: 250px;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  padding: 10px;
}

input[type="text"]:focus {
  outline: 0;
}

[type="submit"] {
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

[type="submit"]:hover {
  cursor: pointer;
  background-color: #bababa;
}

.load-more {
  color: #fff;
  width: 180px;
  visibility: hidden;
  background-color: #4056b4;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 8px;
}

.load-more:hover {
  cursor: pointer;
  border: 2px solid #fff;
  padding: 6.5px;
}

